        <mt-field label="" placeholder="" v-model="data.username"></mt-field>
        <mt-field label="" placeholder="" readonly v-model="data.dept" @click.native="showDept=true"></mt-field>
        <mt-field label="" placeholder="" readonly v-model="data.job" @click.native="showJob=true"></mt-field>
        <mt-field label="" placeholder="" readonly v-model="data.level" @click.native="showLevel=true"></mt-field>
        <mt-field label="" placeholder="" readonly v-model="data.post" @click.native="showPost=true"></mt-field>
        <mt-field label="" placeholder=""type="tel" v-model="data.tel"></mt-field>
        <mt-field label="" placeholder="" type="email" v-model="data.email"></mt-field>
        <mt-actionsheet
          :actions="dept_actions"
          v-model="showDept">
