                method: this.takePhoto
              },{
                name: '',
                method: this.takePhoto
              }],
              job_actions:[{
                name: '',
                method: this.takePhoto
              }, {
                name: '',
                method: this.takePhoto
              }],
              level_actions:[{
                name: '',
                method: this.takePhoto
              }, {
                name: '',
                method: this.takePhoto
              }],
              post_actions:[{
                name: '',
                method: this.takePhoto
              }, {
                name: '',
                method: this.takePhoto
              }],
        }
    },
    methods:{
        takePhoto(e) {
        console.log(e.name);
      },
    },
    components: {
        FooterBar
    }
}
</script>
<style lang="scss" scoped>
    .add-linker{
        padding-top: 48px
    }
</style>



