import Vue from 'vue'
import Router from 'vue-router'
import drive from '../pages/drive'
import detail from '../pages/detail'
import postDynamic from '../pages/postDynamic'  //  add 2017/8/20
import addTask from '../pages/addTask'  //   add 2017/8/20
// import relCustom from '../pages/relCustom' // add 2017/8/21
import login from '../pages/login'
import userinfo from '../pages/userinfo'  // add 2017/8/24
import userinfoEdit from '../pages/userinfoEdit' //  add2017/8/25
import cropinfo from '../pages/cropinfo'  // add 2017/8/26
import lowerLevel from '../pages/lowerLevel'  // add 2017/8/28
import crminfo from '../pages/crminfo' //crm add 2017/8/29
import addLinker from '../pages/addLinker'  // crm -   add 2017/8/30
import editcrminfo from '../pages/editcrminfo' // crm add 2017/8/31
import cloudDisk from '../pages/cloudDisk' // add 2017/8/31
import addressBook from '../pages/addressBook'  // add 2017/9/2
import addClerk from '../pages/addClerk'  // add 2017/9/2
import workFlow from '../pages/workFlow' //  add 2017/9/4
import workFlowDetail from '../pages/workFlowDetail' //  add 2017/9/4
import addWorkFlow from '../pages/addWorkFlow' //  add 2017/9/4
import set from "../pages/set.vue"  //  add 2017/9/6

Vue.use(Router)

// 
// 1.  drive
// 2.  details/id
// 3.  workFlow
// 4. 
// 5. login
// 6. userinfo/loginname
// 7.crm --/crminfo/:id
// 8.crm - 
// 9.crm
// 10.
// 11.
// 12.-
// 13.

export default new Router({
  routes: [
    {
      path: '/',
      name: 'drive',
      component: drive
    },
    {
      path: '/cloud-disk',
      name: 'cloudDisk',
      component: cloudDisk
    },
    {
      path: '/address-book',
      name: 'addressBook',
      component: addressBook
    },
     {
      path: '/userinfo-edit/',
      name: 'userinfo-edit',
      component: userinfoEdit
    },
    {
      path: '/userinfo/:loginname',
      name: 'userinfo',
      component: userinfo
    },
    {
      path: '/cropinfo',
      name: 'cropinfo',
      component: cropinfo
    },
    {
      path: '/addLinker',
      name: 'addLinker',
      component: addLinker
    },
    {
      path: '/addClerk',
      name: 'addClerk',
      component: addClerk
    },
    {
      path: '/crminfo/:id',
      name: 'crminfo',
      component: crminfo
    },
    {
      path: '/editcrminfo',
      name: 'editcrminfo',
      component: editcrminfo
    },
    {
      path: '/lowerLevel/:loginname',
      name: 'lowerLevel',
      component: lowerLevel
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/set',
      name: 'set',
      component: set
    },
    {
      path: '/detail/:id',
      name: 'detail',
      component: detail
    },
    {
      path: '/addTask',
      name: 'addTask',
      component: addTask
    },
    {
      path: '/workFlow',
      name: 'workFlow',
      component: workFlow,
      children:[{
          name:"wfd",
          path: 'detail/:id',
          component: workFlowDetail
      }]
    },
    {
      path: '/addWorkFlow',
      name: 'addWorkFlow',
      component: addWorkFlow
    },
    {
      path: '/postDynamic',
      name: 'postDynamic',
      component: postDynamic
    }
  ]
})



